<template>
<!--SECTION ABOUT-->
<section class="il-section il-section--about">
    <div class="il-container--wrapper">
        <h1 class="il-section--title">{{$parent.titleCompost.title}}
            <span class="il-color--text__dark">{{$parent.titleCompost.title1}} e </span> <span class="il-color--text__red">{{$parent.titleCompost.subTitle}}</span>
        </h1>
        <h2 class="il-section--sub-title il-color--text__very-light text-right">Um breve histrico da Kaizen:</h2>
        <div class="il-about--content">
            <div class="il-box--items">
                <div class="il-item il-item--0 il-box il-color--background__lighten">
                    <h5></h5>
                    <p class="il-content--description il-color--text">O nascimento da Kaizen no dia 02 de maio de 2019  a materializao de um sonho de uma profissional apaixonada pelo movimento. A experincia adquirida ao longo de vrios anos dedicados  atividade fsica, permitiu criar um conceito de excelncia na prestao de servios relacionados a sade, que possibilita o alcance rpido no seu objetivo. A atuao por 19 anos como scia proprietria de outro local, bem como a formao de inmeros instrutores em Pilates e CoreAlign, contriburam para uma base slida do projeto Kaizen.<br>E assim voc encontrar um ambiente acolhedor, clean, sofisticado, com equipamentos de primeira qualidade, com profissionais que buscam aperfeioamento constante para lhe proporcionar uma experincia positiva de movimento.</p>
                </div>
            </div>
            <ul class="il-icons">
                <li>Conhea a <a href="#!" title="Misso da Kaizen" @click.prevent="showMission()">misso</a> da Kaizen</li>
            </ul>
            <div class="il-mission">
                <div class="il-card">
                    <h5 class="il-color--text__gold">Nossa misso</h5>
                    <p class="il-color--white">Transformar a vida dos alunos atravs de um atendimento de elevado nvel tcnico e ao mesmo tempo acolhedor, proporcionando alvio de dores, disposio fsica e facilitando as atividades de vida diria.<br>Estimular o movimento inteligente de forma repetida e sistemtica, criando as condies ideais para uma vida feliz e saudvel.</p>
                        <a href="#!" class="il-close il-color--white" @click.prevent="closeMission()"> X </a>
                </div>
            </div>
        </div>
    </div>
</section>
</template>

<script>
import {
    animeAbout
} from '../../common/animation';
export default {
    name: 'About',
    mounted() {
        this.startAnime();
    },
    data() {
        return {
            whatIcon: 'il-pilates-icon flaticon-side-bending-posture'
        };
    },
    methods: {
        showMission() {
            let card = document.querySelector('.il-mission');
            card.classList.add('il-show--mission');
        },
        closeMission() {
            let card = document.querySelector('.il-mission');
            card.classList.remove('il-show--mission');
        }
    },
    mixins: [animeAbout]
};
</script>
